<template>
  <div class="mood">
    <h2>心情随笔</h2>
    <div v-if="waiting">
      正在请求数据，请稍候...
    </div>
    <ul v-else>
      <li v-for="moodArticle in moodArticleDir">
        {{ moodArticle.createdAt }}
        <div v-html="moodArticle.content"></div>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Mood",
  data(){
    return {
      moodArticleDir:[],
      waiting: true
    }
  },
  created() {
    axios.get('/MoodRecords')
      .then((data) =>{
        // console.log(data.data.results[0])
        data.data.results = data.data.results.reverse();
        setTimeout(() => {
          this.waiting = false;
        },300);
        for (data of data.data.results){
          data.id = data.objectId;
          // console.log(data)
          this.moodArticleDir.push(data);
        }
      });
    // axios.post('/TechArticles',{
    //   title:'test',
    //   content:'testtest123123',
    //   categories:['js'],
    //   author:'zhj'
    // }).then(function (data){
    //   console.log(data);
    // })
  }
}
</script>

<style scoped>
.mood>h2 {
  margin-bottom: 10px;
  padding-left: 8px;
}
.mood>div {
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  background-color: #f60;
  border-radius: 20px;
}
.mood ul li{
  width: 100%;
  padding: 30px 30px;
  margin-bottom: 10px;
  font-size: 12px;
  background-color: pink;
  border-radius: 20px;
}
.mood ul li div {
  padding: 10px 0 0 0;
}
</style>



