<template>
  <div class="technology">
    <div class="wait" v-if="waiting">
      正在请求数据，请稍候...
    </div>
    <div v-else>
      <h2>{{ techArticle.title }}</h2>
      <div v-html="techArticle.content"></div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Technology",
  data(){
    return {
      techArticle:{},
      waiting: true
    }
  },
  inject:["routerRefresh"],
  beforeRouteUpdate(to, from, next) {
    this.routerRefresh(); //路由销毁重建方法
    next();
  },
  created() {
    axios.get('/TechArticles/'+this.$route.params.id)
      .then((data) =>{
        // console.log(data.data)
        setTimeout(() => {
          this.waiting = false;
        },300);
        this.techArticle = data.data
      })
    // axios.post('/TechArticles',{
    //   title:'test',
    //   content:'testtest123123',
    //   categories:['js'],
    //   author:'zhj'
    // }).then(function (data){
    //   console.log(data);
    // })
  }
}
</script>

<style scoped>
.technology h2 {
  padding: 15px 0 15px 8px;
}
.technology .wait {
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  background-color: #f60;
  border-radius: 20px;
}
.technology >>> h3 {
  padding: 10px 0;
}
.technology >>> p {
  text-indent: 2em;
}
</style>



