<template>
  <div class="techDir">
    <h2>技术分享</h2>
    <div v-if="waiting">
      正在请求数据，请稍候...
    </div>
    <ul v-else>
      <li v-for="techArticle in techArticleDir">
        <div class="typeImg">
          <img :src="'../../static/'+techArticle.categories[0]+'.jpg'" alt="6666">
        </div>
        <div class="content">
          <Router-link :to="'/technology/'+techArticle.id">
            <h3>{{ techArticle.title }}</h3>
          </Router-link>
          <div v-html="techArticle.content.slice(0,50)+'...'"></div>
          <div>
            <ul>
              <li>作者:{{ techArticle.author }}&nbsp;&nbsp;创建时间:{{techArticle.createdAt}}</li>
              <li>分类:{{ techArticle.categories.join('|') }}&nbsp;&nbsp;文章id:{{techArticle.id}}</li>
            </ul>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "TechnologyDirectory",
  data(){
    return {
      techArticleDir:[],
      waiting: true
    }
  },
  mounted() {
    axios.get('/TechArticles')
    .then((data) =>{
      // console.log(data.data.results[0])
      data.data.results = data.data.results.reverse();
      setTimeout(() => {
        this.waiting = false;
      },300);
      for (data of data.data.results){
        data.id = data.objectId
        // console.log(data)
        this.techArticleDir.push(data)
      }
    })
    // axios.post('/TechArticles',{
    //   title:'test',
    //   content:'testtest123123',
    //   categories:['js'],
    //   author:'zhj'
    // }).then(function (data){
    //   console.log(data);
    // })
  }
}
</script>

<style scoped>
.techDir>h2 {
  margin-bottom: 10px;
  padding-left: 8px;
}
.techDir>div {
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  background-color: #f60;
  border-radius: 20px;
}
.techDir ul li{
  width: 100%;
  padding: 20px;
  margin-bottom: 10px;
  background-color: pink;
  border-radius: 20px;
  overflow: hidden;
}
.techDir ul li .typeImg {
  float: left;
  width: 120px;
  height: 120px;
  border-radius: 16px;
  overflow: hidden;
}
.techDir ul li .typeImg img {
  width: 120px;
  height: 120px;
}
.techDir ul li .content {
  float: left;
  width: 76%;
  padding-left: 10px;
  overflow: hidden;
}
.techDir ul li .content div:nth-of-type(1) {
  min-height: 30px;
  overflow: hidden;
}
.techDir ul li .content div:nth-of-type(1) >>> h3,
.techDir ul li .content div:nth-of-type(1) >>> p {
  font-size: 14px;
}
.techDir ul li .content div:nth-of-type(2) ul {
  margin: 6px 0 0 0;
}
.techDir ul li .content div:nth-of-type(2) ul li {
  margin: 0;
  padding: 0;
  width: auto;
  height: auto;
  font-size: 12px;
  color: #666;
}
.techDir ul li .content a {
  display: inline-block;
  padding-bottom: 10px;
}
</style>



