<template>
  <div class="memoryDir">
    <h2>韶华追忆</h2>
    <div v-if="waiting">
      正在请求数据，请稍候...
    </div>
    <ul v-else>
      <li v-for="memoryArticle in memoryArticleDir">
<!--        <div class="typeImg">-->
<!--          <img :src="'../../static/'+memoryArticle.categories[0]+'.jpg'" alt="6666">-->
<!--        </div>-->
        <div class="content">
          <Router-link :to="'/memory/'+memoryArticle.id">
            <h3>{{ memoryArticle.title }}</h3>
          </Router-link>
          <div v-html="memoryArticle.content.slice(0,50)+'...'"></div>
          <div>
            <ul>
              <li>作者:{{ memoryArticle.author }}&nbsp;&nbsp;创建时间:{{memoryArticle.createdAt}}</li>
              <li>文章id:{{memoryArticle.id}}</li>
            </ul>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "MemoryDirectory",
  data(){
    return {
      memoryArticleDir:[],
      waiting: true
    }
  },
  created() {
    axios.get('/MemoryArticles')
      .then((data) =>{
        // console.log(data.data.results[0])
        data.data.results = data.data.results.reverse()
        setTimeout(() => {
          this.waiting = false;
        },300);
        for (data of data.data.results){
          data.id = data.objectId
          // console.log(data)
          this.memoryArticleDir.push(data)
        }
      })
  }
}
</script>

<style scoped>
.memoryDir>h2 {
  margin-bottom: 10px;
  padding-left: 8px;
}
.memoryDir>div {
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  background-color: #f60;
  border-radius: 20px;
}
.memoryDir ul li{
  width: 100%;
  padding: 15px;
  margin-bottom: 10px;
  background-color: pink;
  border-radius: 20px;
  overflow: hidden;
}
.memoryDir ul li .typeImg {
  float: left;
  width: 120px;
  height: 120px;
  border-radius: 16px;
  overflow: hidden;
}
.memoryDir ul li .typeImg img {
  width: 120px;
  height: 120px;
}
.memoryDir ul li .content {
  float: left;
  width: 100%;
  padding-left: 10px;
  overflow: hidden;
}
.memoryDir ul li .content div:nth-of-type(1) {
  min-height: 30px;
  overflow-wrap: break-word;
  overflow: hidden;
}
.techDir ul li .content div:nth-of-type(1) >>> h3,
.techDir ul li .content div:nth-of-type(1) >>> p {
  font-size: 14px;
}
.memoryDir ul li .content div:nth-of-type(2) ul {
  margin: 3px 0 0 0;
}
.memoryDir ul li .content div:nth-of-type(2) ul li {
  margin: 0;
  padding: 0;
  width: auto;
  height: auto;
  font-size: 12px;
  color: #666;
}
.memoryDir ul li .content a {
  display: inline-block;
  padding-bottom: 10px;
}
</style>



