<template>
  <div class="memory">
    <div class="wait" v-if="waiting">
      正在请求数据，请稍候...
    </div>
    <div v-else>
      <h2>{{ memoryArticle.title }}</h2>
      <div v-html="memoryArticle.content"></div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Memory",
  data(){
    return {
      memoryArticle:{},
      waiting: true
    }
  },
  created() {
    axios.get('/MemoryArticles/'+this.$route.params.id)
      .then((data) =>{
        // console.log(data.data)
        setTimeout(() => {
          this.waiting = false;
        },300);
        this.memoryArticle = data.data;
      });
  }
}
</script>

<style scoped>
.memory h2 {
  padding: 15px 0 15px 8px;
}
.memory .wait {
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  background-color: #f60;
  border-radius: 20px;
}
.memory >>> h3 {
  padding: 10px 0;
}
.memory >>> p {
  text-indent: 2em;
}
</style>



